
  @import url('https://fonts.googleapis.com/css2?family=Crimson+Text&family=Montserrat:wght@100&display=swap');

  .Topcontent{
    color: white;
  }

  .Topcontent_container{
    position: absolute;
    top: 300px;
    right: 150px;

  }
  .Topcontent_container>p{
    letter-spacing: 1px;
    text-align: left;
    padding-left: 3px;
    padding-top: 20px;
  }
  .Topcontent_container>a>button{
    margin-top: 30px;
    text-align: left;
    margin-left: 5px;
    border:none;
    border-radius: 2px;
    outline: none;
  }
  .Topcontent_container>h1{
    font-size: 39px;
    font-family: "Montserrat",sans-serif;
  }.download_but{
    color: white;
    background-color: #f64c08;
    font-size: 14px;
    padding: 10px 30px;
   font-family: "Montserrat",sans-serif;
    font-weight: 300;
    cursor: pointer;
    margin-right: 20px;


  }.download_but:hover{
    color: #f64c08;
    background-color: white;
    font-weight: 600;
  }
  
  .work_but{
    color: white;
    border: 1px solid white !important;
    background-color: transparent;
    font-size: 14px;
    padding: 9px 45px;
    font-family: "Montserrat",sans-serif;
    cursor: pointer;
    font-weight: 300;


  }
  .work_but:hover{
    color: black;
    background-color: white;
    font-weight: 600;
  }