.contact{
    display: flex;
    flex-direction: column;
    color: white;
    justify-content: center;
    margin: 200px 0 100px;
    align-items: center;
    
   
}.conatct_container{
    display: flex;
    border: 1px solid white;
    width: 600px;
    height: 200px;
    flex-direction: column;
    align-items: center;
    padding: 30px;
    margin-top: 20px
    
    

}.conatct_container>p{
    font-size: 20px;
    text-align: left;
    padding-bottom: 30px;
    margin-bottom: 20px;
}.conatct_container>p>span{
    color: #f64c08;
}
.contact__icon>{
    display: flex;
    justify-content: space-evenly;
    align-items: center;

}.contact___icon>a>button{
    color: white !important;
}